.mintzy {
    display: flex;
    font-family: 'Gugi', cursive;
    background-color:rgb(24, 24, 24);
    height: 100vh;
    z-index: -8;
    background-size: cover;
    margin-top: -30px;
    background-attachment: fixed;

}
.mintbtn {
    position: relative;
    padding: 10px;
    font-size: 18px;
    height: 40px;
    border: none;
    z-index: 800000;
    background-color: rgb(255, 217, 0);

}
.pic{
    height: 500px;
    padding: 30px;
    
}
.picture-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    width: 100vw;
    justify-content: space-between;
  }
  
  .picture-box {


    height: 40vh;
    width: auto;
    margin:0 auto;
    margin-top: 40px;
  }
  .picture-box img {

    height: 35vh;
    width: auto;
  }
  
  @media screen and (max-width: 768px) {
    .picture-container {
      flex-direction: column;
      align-items: center;
    }
    
    .picture-box {
      flex-basis: 100%;
    }
  }