body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: left;

}

.App-logo {

  height: 100px;
  padding-right: 150px;
}

.App-header {
  background-color: #2b2b2b;
  height: 130px;
  color: white;
  display: flex;
  flex-direction:row;

  

}

.App-title {
  font-size: 1.3em;
  margin: 0 20px;
  line-height: 1.7rem;
  margin-top: 50px;
  transition: all ease-in-out .5s;
}
.App-title:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  font-size: 1.3em;
  color: red;
  cursor: pointer;
}
.App-intro {
  margin-top: 30px;
  font-size: large;
}



.mintzy {
    display: flex;
    font-family: 'Gugi', cursive;
    background-color:rgb(24, 24, 24);
    height: 100vh;
    z-index: -8;
    background-size: cover;
    margin-top: -30px;
    background-attachment: fixed;

}
.mintbtn {
    position: relative;
    padding: 10px;
    font-size: 18px;
    height: 40px;
    border: none;
    z-index: 800000;
    background-color: rgb(255, 217, 0);

}
.pic{
    height: 500px;
    padding: 30px;
    
}
.picture-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    width: 100vw;
    justify-content: space-between;
  }
  
  .picture-box {


    height: 40vh;
    width: auto;
    margin:0 auto;
    margin-top: 40px;
  }
  .picture-box img {

    height: 35vh;
    width: auto;
  }
  
  @media screen and (max-width: 768px) {
    .picture-container {
      flex-direction: column;
      align-items: center;
    }
    
    .picture-box {
      flex-basis: 100%;
    }
  }
